.eachDay {
  text-align: center;
  margin: 0 10px;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 0;
  min-height: 100%;
}

.eachDay .card {
  padding: 6px;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
}

.eachDay .card button {
  border: none;
  background-color: rgba(255, 255, 255, 0);
  outline: none;
}

.eachDay .cardDetailsMobile {
  display: none;
}

.eachDay .cardBody .cardText {
  text-transform: capitalize;
}

@media only screen and (max-width: 480px) {
  .eachDay {
    margin-top: 10px;
  }
  
  .eachDay .cardDetailsMobile {
    display: block;
  }

  body #cardDetails {
    display: none;
  }
}