
body #cardDetailsPopup {
  position: absolute;
  top: 40%;
  left: 40%;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 5px;
  padding: 30px 10px 20px 10px;
  display: none;
}

body #cardDetailsPopup .weatherDetails {
  width: 300px;
  text-align: center;
}

body #cardDetailsPopup .weatherDetails h1 {
  font-size: 30px;
  font-family: 'Roboto Slab', serif;;
}

body #cardDetailsPopup .weatherDetails .close {
  top: -23px;
  right: -6px;
  position: absolute;
  cursor: pointer;
  display: block;
  background-color: rgba(255, 0, 0, 0.8);
  font-size: 12px;
  padding: 5px;
}

@media only screen and (max-width: 480px) {
  body #cardDetailsPopup {
    position: fixed;
    left: 20px;
    top: 40%;
  }
}