.nightFormContainer, 
.formContainer {
  margin-top: 30px;
}

.nightFormContainer .row, 
.formContainer .row {
  justify-content: center;
}

.nightFormContainer .row form, 
.formContainer .row form {
  text-transform: capitalize;
}

.nightFormContainer .row form label, 
.formContainer .row form label {
  margin-right: 10px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
}

.nightFormContainer .row form label {
  color: #b1b1b1;
}

.nightFormContainer .row form input, 
.formContainer .row form input {
  text-transform: capitalize;
  background-color: rgba(255, 255, 255, 0.3);
  color: #000;
  border-radius: 5px;
  border: none;
  padding: 5px;
  font-weight: 600;
}

.nightFormContainer .row form button, 
.formContainer .row form button {
  margin-left: 10px;
  text-transform: capitalize;
  color: #000;
  outline: none;
  border: none;
  background-color: #b1b1b1;
  padding: 3px;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #76ff03;
}

@media only screen and (max-width: 480px) {

  .nightFormContainer .row form ,
  .formContainer .row form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .nightFormContainer .row form * ,
  .formContainer .row form * {
    margin-top: 10px;
  }

  .nightFormContainer .row form input,
  .formContainer .row form input {
    width: 100%;
  }

  .nightFormContainer .row form button ,
  .formContainer .row form button {
    width: 120px;
  }
}